@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@600;700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;

.bayu {
  font-family: "Quicksand";
  font-style: normal;
  font-weight: 661;
  font-size: 27.9812px;
  line-height: 35px;
  /* identical to box height */

  letter-spacing: -0.02em;

  /* Iris/100 */

  color: #5d5fef;
}
* {
  padding: 0px;
  margin: 0px;
  font-family: "Quicksand", sans-serif;
  font-size: 14px;
}
.card {
  background-color: #5d5fef;
  height: 18rem;
  width: 15rem;
  border-radius: 1rem;
  position: relative;
  overflow: hidden;
}
.card::before {
  content: "";
  background: linear-gradient(to bottom, #6c63ff, #00b4d8);
  height: 20rem;
  width: 12rem;
  border-radius: 50% 50% 0 50%;
  border: 0.1rem solid #fff;
  position: absolute;
  right: -4rem;
  bottom: -6rem;
}
.card::after {
  content: "";
  background: linear-gradient(to bottom, #a5a6f6); /* gradien bulat bundar */
  height: 20rem; /* tinggi elemen sebelumnya */
  width: 12rem; /* lebar elemen sebelumnya */
  border-radius: 70% 90% 60% 80%; /* radius bulat bundar */
  border: 0.1rem solid #fff; /* ketebalan border */
  position: absolute; /* posisi absolut */
  right: -6rem; /* posisi elemen relatif terhadap kanan */
  bottom: -6rem; /* posisi elemen relatif terhadap bawah */
}
.card2 {
  background-color: #fafafa;
  width: 227px;
  height: 595px;
  position: relative;
  overflow: hidden;
}
.card3 {
  background-color: #ffffff;
  height: 3.5rem;
  width: 13rem;
  position: relative;
  text-align: start;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.05);
  border-radius: 8px; /* tambahkan shadow dengan properti box-shadow */
}

.card3::before {
  content: "1"; /* menambahkan angka "1" pada card */
  position: absolute;
  top: 0;
  right: 0;
  color: #ffffff;
  width: 23.81px;
  height: 23.44px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
  font-weight: bold;
  background: #f17300;
  box-shadow: 2px 2px 0px #464646;
  transform: rotate(8deg);
}
.card4 {
  background-color: #5d5fef;
  height: 167px;
