button.bgblur {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
}
/* mobile */
@media only screen and (max-width: 768px) {
  .border-r {
    border: none;
  }
  .spave-y-27,
  .pb-60 {
    display: none;
  }
  .bayu {
    display: none;
  }
  .flex.flex-row.item-center {
    margin-bottom: 2rem;
  }
  .text-xs {
    font-size: 0.875rem;
  }
  .mb-7 {
    margin-bottom: 1.5rem;
  }
  .mr-4 {
    margin-right: 1rem;
  }
  .flex.flex-row.items-center.text-gray-400.hover:text-blue-700 {
    margin-bottom: 0;
    padding: 0.75rem 1rem;
    border-radius: 0.375rem;
  }
  .flex-1 {
    flex-basis: 100%;
  }

  .card4 {
    width: 100%;
  }

  .grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .card4 {
    padding: 1rem;
    margin-top: 1rem;
  }

  .card4 .flex {
    flex-direction: column;
  }

  .card4 .flex .pl-4 {
    padding: 0;
  }

  .card4 .flex .pt-4 {
    padding-top: 1rem;
  }

  .card4 .flex .text-xl {
    font-size: 1.5rem;
  }

  .card4 .flex .text-sm {
    font-size: 0.8rem;
  }

  .card4 .flex .scapce-x-5 {
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .card4 .flex .pt-4 .bg-indigo-400 {
    width: 100%;
  }

  .card4 .flex .pt-4 .border {
    margin-top: 1rem;
    width: 100%;
  }

  .card4 .flex .pt-4 .bg-indigo-500 {
    margin-top: 1rem;
    width: 100%;
  }

  .card4 .flex .pt-4 .bgblur {
    margin-top: 1rem;
    width: 100%;
  }
}
